#playlist-page {
	width: 50%;
	min-width: 400px;
	display: flex;
	flex-direction: column;
	margin: auto;
	margin-top: 10px;
	padding: 20px 20px;
	border-radius: 20px;
	box-shadow: 0px 0px 500px 0px #00bcd4;
}

#playlist-page > * {
	margin: 10px 0px;
}

#playlist-page #util-btns {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.playlist-item-container {
	width: 100%;
	display: flex;
	flex-direction: row;
}

.playlist-item-container > * {
	padding: 0px 10px;
}

.playlist-item-container img {
	width: 90px;
}

.playlist-item-container .items-mid {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}
